/* App.css */

body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
  margin: 0;
  padding: 20px;
}

.heading {
  text-align: center;
  color: #333;
  font-size: 2.5rem;
}

.subheading {
  margin-top: 20px;
  font-size: 1.5rem;
  color: #555;
}

.getmemo {
  margin-top: 30px;
  font-size: 1.5rem;
  color: #555;
}

/* Container to hold the form elements */
formalign {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
  gap: 20px; /* space between input fields */
}

/* Style for the heading */
.heading {
  font-size: 32px;
  color: #007bff;
  font-weight: bold;
  margin-bottom: 20px;
  text-align: center;
  animation: fadeInDown 0.8s ease-out;
}

/* Style for the subheading */
.subheading {
  font-size: 24px;
  color: #444;
  margin-bottom: 20px;
  text-align: center;
}
.formalign {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
  gap: 20px; /* Space between each input field */
}
/* Style for the input fields */
input.form {
  flex-direction: column;
  padding: 12px 20px;
  font-size: 16px;
  border: 2px solid #ddd;
  border-radius: 5px;
  outline: none;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
  width: 300px;
}

/* Input field hover and focus effect */
input.form:hover {
  border-color: #888;
}

input.form:focus {
  border-color: #007bff;
  box-shadow: 0 0 10px rgba(0, 123, 255, 0.2);
}

/* Subtle animations for the form inputs and headings */
@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Add animation to input fields */
input.form {
  animation: fadeInUp 0.8s ease-out;
}

/* Button container alignment */
.btndiv {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px; /* Adjust spacing as needed */
}

/* Button styling */
.btn {
  position: relative;
  display: inline-block;
  padding: 15px 25px;
  font-size: 15px;
  font-weight: bold;
  color: #18181a;
  background: #00ec47;
  border: none;
  cursor: pointer;
  overflow: hidden;
  user-select: none;
  transition: color 0.3s ease, background 0.3s ease;
}

.btn:hover {
  color: white;
}

/* Button hover effect */
.btn:after {
  content: "";
  position: absolute;
  bottom: -100%;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  transform-origin: bottom center;
  transition: transform 0.6s cubic-bezier(0.48, 0, 0.12, 1);
  transform: skewY(9.3deg) scaleY(0);
  z-index: -1;
}




.fetchmemo {
  background-color: #007BFF; /* Blue background */
  color: white; /* White text */
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1.2rem;
  transition: background-color 0.3s ease;
}

.fetchmemo:hover {
  background-color: #0056b3; /* Darker blue on hover */
}

.list {
  list-style-type: none; /* Remove bullet points */
  padding: 0;
  margin-top: 20px;
}

.list li {
  background: #fff; /* White background for list items */
  border: 1px solid #ddd; /* Light grey border */
  border-radius: 5px; /* Rounded corners */
  padding: 10px;
  margin: 5px 0; /* Space between items */
}

.list li strong {
  color: #007BFF; /* Color for names */
}
